* {
  box-sizing: border-box;
  font-size: 80%;
  line-height: 2rem;
}

body {
  font-family: -apple-system, Poppins, "Open Sans", "Helvetica Neue", sans-serif;
  background: #153040;
  color: #ffffff;
}

a:hover {
  text-decoration: none;
}

h1 {
  font-size: 4rem;
  font-weight: bold;
}

h2 {
  color: #ffc619;
  font-size: 2.5rem;
  font-weight: lighter;
  padding: 3rem 0;
  text-align: center;
}

h3 {
  font-size: 2rem;
}

p .intro {
  font-size: 1.5rem;
  color: #ffc619;
}

p {
  font-size: 1.1rem;
}

.section-wrapper {
  max-width: 1600px;
  margin: 0 auto;
}

/* 980px / 16px = 61.25em */
@media (max-width: 61.25em) {
  html {
    font-size: 90%;
  }
}
/* 460px / 16px = 28.75em */
@media (max-width: 28.75em) {
  html {
    font-size: 80%;
  }
}
/* - - NAV  START - - */
#navbar {
  top: 0;
  position: fixed;
  display: flex;
  background: #182833;
  padding: 0.5rem 5rem;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  z-index: 99;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
  flex-wrap: wrap;
  height: 5rem;
}

#nav-links {
  display: flex;
}

#nav-links li {
  padding: 1em;
}

#nav-links li a {
  text-decoration: none;
  color: #ffffff;
  font-size: 1.5rem;
  font-weight: 500;
}

#nav-links li a:visited {
  color: inherit;
}

#nav-links li a:hover {
  color: #ffc619;
}

svg {
  width: 3rem;
  fill: #ffc619;
}

#checkbox-toggle {
  display: none;
}

.menu-btn i {
  color: #fff;
  font-size: 2rem;
  cursor: pointer;
  display: none;
}

.menu-btn i:hover {
  color: #ffc619;
}

@media (max-width: 965px) {
  .menu-btn i {
    display: block;
  }

  #checkbox-toggle:checked ~ .menu-btn .fa-bars:before {
    content: "\f00d";
  }

  #nav-links {
    position: fixed;
    top: 5rem;
    padding-top: 5rem;
    left: -100%;
    background: #1f4762;
    height: 100vh;
    width: 100%;
    text-align: center;
    display: block;
    transition: all 0.3s ease;
  }

  #nav-links li a {
    width: 100%;
    margin-left: -100%;
    display: block;
    font-size: 1.5rem;
    transition: 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  }

  #checkbox-toggle:checked ~ ul {
    left: 0;
  }

  #checkbox-toggle:checked ~ ul li a {
    margin-left: 0px;
  }

  #nav-links li a:focus ~ ul {
    left: 0;
  }
}
/* - - NAV  END - - */
/* - - WELCOME SECTION  START - - */
.welcome-section {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(../img/hero-bg.png);
  background-position-x: 60%;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100vh;
  align-items: center;
  text-align: center;
}

.cv-btn {
  background: #ffc619;
  border-style: solid;
  border-color: #ffc619;
  border-width: 0.1rem;
  border-radius: 1.5rem;
  color: #153040;
  font-weight: bold;
  margin-top: 1rem;
  padding: 0.3rem 1rem;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
}

.cv-btn:hover {
  color: #ffc619;
  border-color: #ffc619;
  background: #153040;
}

.social {
  display: flex;
  margin-top: 1rem;
}

.social-btn {
  padding: 0.5rem 1rem;
  color: #ffc619;
  font-size: 2rem;
}

.social-btn:hover {
  color: #ffc619;
  transform: scale(1.5);
}

/* - - WELCOME SECTION  END - - */
/* - - ABOUT ME START- - */
#about-me {
  padding: 4rem 0;
  background: #1f4762;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
}

.about-me-container {
  display: flex;
  margin: 0 3rem;
  justify-content: center;
}

.about-me-header {
  text-align: left;
}

.about-box {
  max-width: 40rem;
}

.profile-img {
  width: 15rem;
  position: relative;
  border-radius: 50%;
  background: #ffc619;
  margin: 3rem;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
}

@media (max-width: 965px) {
  .about-me-container {
    display: block;
    padding: 1rem 0;
  }

  .about-box {
    padding: 0;
    max-width: 100%;
  }

  .profile-img {
    display: block;
    float: none;
    margin: 0 auto;
    justify-content: center;
  }

  .about-me-header {
    text-align: center;
  }
}
/* - - ABOUT ME END- - */
/* - - PROJECTS START- - */
#projects {
  padding: 4rem 0;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-gap: 4rem;
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  margin-bottom: 6rem;
  padding: 0 2rem;
}

.project-image {
  width: 100%;
  object-fit: cover;
  border-radius: 0.5rem;
}

.project {
  width: 100%;
  background: #1f4762;
  border-radius: 0.5rem;
  padding: 0.3rem 0.3rem 0 0.3rem;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
}

.project p {
  color: #ffffff;
  text-align: center;
}

.project:hover {
  background: #ffc619;
  transform: scale(1.1);
}

@media (max-width: 280px) {
  .projects-grid {
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  }
}
/* - - PROJECTS END- - */
/* - - CONTACT START- - */
#contact-section {
  padding: 4rem 0;
  background: #182833;
  box-shadow: -3px -3px 5px rgba(0, 0, 0, 0.4);
}

.message-wrapper {
  max-width: 40rem;
  padding: 0 1rem;
  margin: 0 auto;
}

.msgcontrol:focus {
  border-color: #ffc619;
  outline: 0;
  box-shadow: 0 0 0 0.2rem #ffc619;
}

#message-fullname,
#message-email,
#message-subject,
#message-body {
  height: 2.8rem;
  background-clip: padding-box;
  border-radius: 0.3rem;
  border: none;
  padding: 1rem;
  font-family: inherit;
  font-size: inherit;
  margin: 0.5rem 0;
  width: 100%;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
}

.line1 {
  display: flex;
  gap: 0.5rem;
}

#message-subject {
  width: 100%;
}

#message-submit {
  height: 2.8rem;
  border-radius: 0.3rem;
  width: 12rem;
  border: none;
  display: block;
  font-family: inherit;
  font-size: 1rem;
  font-weight: bold;
  margin-top: 1rem;
  background: #ffc619;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
}

#message-submit:hover {
  background-color: #153040;
  cursor: pointer;
  border: solid #ffc619;
  color: #ffc619;
  border-width: 0.1rem;
}

#message-body {
  min-height: 150px;
  width: 100%;
  padding: 0.625rem;
  resize: none;
}

/* - - CONTACT END- - */
footer {
  background: #182833;
  text-align: center;
  font-size: 0.8rem;
  padding-top: 10rem;
}